import React from 'react';
import ReactDOM from 'react-dom/client';
import App from './App';
import {ApolloProvider, ApolloClient, InMemoryCache, ApolloConsumer} from "@apollo/client"
import { getAllCharacters} from './queries/apollo';
import { STATE } from './config';

const root = ReactDOM.createRoot(document.getElementById('root'))

const queryToObject = queryString => {
  const pairsString = queryString[0] === '?' ? queryString.slice(1) : queryString
  const pairs = pairsString
    .split('&')
    .map(str => str.split('=').map(decodeURIComponent))
  return pairs.reduce((acc, [key, value]) => key ? { ...acc, [key]: value } : acc, {})
}

if (window.location.search) {
  const params = queryToObject(window.location.search)
  if (params.state === STATE && window.opener) {
    window.opener.postMessage(params)
  }
}

const cache = new InMemoryCache(
  {
    typePolicies: {
      Characters: {
        keyFields: ["results"],
        fields: {
          resuls: {
            keyArgs:false,
            merge(existing = [], incoming) {
              return [...existing, ...incoming];
            },
          }
        }
      }
    }
  }
)


const client = new ApolloClient({
  uri: "https://rickandmortyapi.com/graphql",
  cache: cache
})

root.render(
  <React.StrictMode>
    <ApolloProvider client={client}>
      <App/>
    </ApolloProvider>
  </React.StrictMode>
);

